.react-chatbot-kit-chat-bot-avatar-container,
.avatar {
    content: url("chatbot icon.png");
    background-color: white;
}

.avatar {
    z-index: 99998;
    position: fixed;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: white;
    cursor: pointer;
    right: 3%;
    bottom: 5%;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
}

.avatar:hover {
    transform: scale(1.1);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.react-chatbot-kit-chat-header {
    background-color: #011638;
    color: white;
}

.react-chatbot-kit-chat-bot-message>span {
    color: black;
}

.react-chatbot-kit-chat-container {
    position: fixed !important;
    display: block !important;
    background-color: white;
    right: 11.5%;
    bottom: 10%;
    box-shadow: 5px 5px 13px rgba(91, 81, 81, 0.4);
    border-radius: 7px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.chatbot-window {
    position: fixed !important;
    transition: opacity 0.3s ease, z-index 0s 0.3s; /* Add delay to z-index transition */
    opacity: 0;
    pointer-events: none;
    z-index: -9999;
}

.chatbot-window.active {
    position: fixed !important;
    display: block !important;
    transition: opacity 0.3s ease, z-index 0s; /* No delay when becoming active */
    opacity: 1 !important;
    pointer-events: auto;
    z-index: 9999; 
}


.react-chatbot-kit-chat-message-container {
    background-color: white;
}

.react-chatbot-kit-chat-btn-send {
    background-color: #011638;
    cursor: pointer;
    transition: background-color 0.3s ease;
    height: 41.7px;
    width: 95px;
}

.react-chatbot-kit-chat-btn-send:hover {
    background-color: #012c71;
}

.react-chatbot-kit-chat-bot-message {
    background-color: #a3c6c7;
}

.react-chatbot-kit-chat-bot-message-arrow {
    border-right: 8px solid #a3c6c7;
}

.react-chatbot-kit-chat-input {
    background-color: white;
}

.react-chatbot-kit-chat-input:focus {
    outline: none;
}

.react-chatbot-kit-chat-input-form {
    position: block;
    margin-top: 0em !important;
    width: 100% !important;
    margin-bottom: 0 !important;
    bottom: 0 !important;
}